<template>
    <div class="tax-widget">
    <div class="row">
        <div class="col-12">
            <div class="alert alert-danger"> Trigger Warning: upsetting tax information </div>
        </div>
    </div>
    <div class="row">
        <div class="col-12">
            <canvas id="tax-chart"></canvas>
        </div>
    </div>
    <div class="row">
        <div class="col-6">
            <!-- <div class="row"> {{ data }} </div>
            <div class="row"><allocation-input></allocation-input></div>
            <div class="row"><allocation-input></allocation-input></div> -->
        </div>
        </div>
        <div class="col-6">
            chart
        </div>
    </div>
    <!-- <vue-json-pretty :data=chartData_A></vue-json-pretty> -->
    <!--<vue-json-pretty :data=taxinfo_selected></vue-json-pretty>-->
    </div>
</template>

<script>
import VueJsonPretty from 'vue-json-pretty'
import Chart from 'chart.js';   
import { get_pension_limit, get_tax_interp, linspace, chart_data_builder } from './tax.js'

var split_xy = function(data) {
    return {x: data.map(xx => xx[0]), y: data.map(xx => xx[1])}
}

export default {
    components: {
        VueJsonPretty
    },
    name: 'tax-app',
    props: {
        msg: String,
    },
    data () {
        return {
            currencyMap: {'uk': 'GBP'},
            jurisdiction : "uk",
            year : "2018-2019",
            x_min : 0,
            x_max : 250000,
            // allocations stuff
            periods: 3,
            mean_gross: 200000,
            initial_room: 0,
            internal_allocations: [0.5, 0.5, 0.5]
        }
    },
    computed : {
        taxinfo: function() {
            return this.fetchData()
        },
        taxinfo_selected: function() {
            // TODO handle if many exist
            return this.taxinfo.filter(x => (x.country == 'uk') && (x.year == '2018-2019'))[0]
        },
        funcs: function() {
            // involves a lot scope, whatever
            var xy = split_xy(this.taxinfo_selected['data_income_tax']['data']) // ignore the type for now, it is fixed
            var F = get_tax_interp(xy.x, xy.y)
            var G_xy = split_xy(this.taxinfo_selected['data_pension_limit']['data'])
            var G = get_pension_limit(G_xy.x, G_xy.y)
            var obj = {G: G.G, m_F: F.m_F, F: F.F, x: xy.x, y: xy.y, G_x: G_xy.x, G_y: G_xy.y}
            return obj
        },
        xrange: function() {
            return linspace(this.x_min, this.x_max, 10)
        },
        chartData_A: function() {
            // call the funcs and get the data you will want to plot
            // TODO use better xrange later
            var xrange = this.funcs.x.slice()
            xrange.push(this.x_max)
            var y = xrange.map(this.funcs.F)
            var m_y = xrange.map(this.funcs.m_F)

            // pension
            var G_xrange = this.funcs.G_x.slice()
            G_xrange.push(this.x_max)
            var G_y = G_xrange.map(this.funcs.G)
            var data = chart_data_builder(xrange, y, m_y, G_xrange, G_y, 'gross income (' + this.currencyMap['uk'] + ')')
            // console.log(data)
            return data
        }
    },
    methods: { // uncache-ables
        fetchData() {
            var obj = require('./taxinfo.json')
            return obj
        },
        createChart(chartId, chartData) {
            const ctx = document.getElementById(chartId);
            if (ctx === undefined) {
                throw('ctx undef')
                }
            const myChart = new Chart(ctx, {
                type: chartData.type,
                data: chartData.data,
                options: chartData.options,
            });
            return myChart // don't strictly need but eslint complains unused if not returned
        }
    },
    mounted() {
        this.fetchData()
        this.createChart('tax-chart', this.chartData_A);
    }
}
</script>

