function assert(condition, message) {
  if (!condition) {
    message = message || "Assertion failed";
    if (typeof Error !== "undefined") {
      throw new Error(message);
    }
    throw message; // Fallback
  }
}

var x_max = 10000000 // some big number to make the interp work

function get_pension_limit(x, y) {
    // the amount of pension room you have in a given year
    assert(x[0] == 0, "x[0] should be zero got " + x[0])

    x = x.slice()
    y = y.slice()
    x.unshift(0) // insert zero at the beginning to have flat extrap to the left
    y.unshift(y[y.length-1])
    x.push(x_max)
    y.push(y[y.length-1]) // flat extrap on the right as well

    var G = function(xx) {
      return PiecewiseLinear(x, y)(xx)
    }
    return {G: G}
}

function get_tax_interp(x, y) {
    // x is income, y is lhs marginal rate
    // marginal and total tax functions
    // returns two functions
    assert(y[0] == 0, "y[0] should be zero got " + y[0])
    assert(x[0] == 0, "x[0] should be zero got " + x[0])

    x = x.slice() // copy, TODO check on this
    y = y.slice()
    x.push(x_max)
    y.push(y[y.length-1])

    var tax = [0]; // cumulative of tax paid up to x
    for (var i = 0; i <= x.length-2; i++) {
        var dtax = (x[i+1] - x[i]) * y[i]
        tax.push(dtax + tax[i])
    }
    var m_F = function(xx) {
        return PiecewiseConstant(x, y)(xx)
    }

    var F = function(xx) {
        return PiecewiseLinear(x, tax)(xx)
    }
    return {m_F: m_F, F: F}
}

