function PiecewiseConstant(xs, ys) {
  return function(x) {
    var lo = 0, hi = xs.length - 1;
    // bisection
    while (hi - lo > 1) {
      var mid = (lo + hi) >> 1; // bitwise shift (div by 2)
      if (x < xs[mid]) hi = mid;
      else lo = mid;
    }
    return ys[lo]
  };
}

function PiecewiseLinear(xs, ys) {
  return function(x) {
    var lo = 0, hi = xs.length - 1;
    // bisection
    while (hi - lo > 1) {
      var mid = (lo + hi) >> 1;
      if (x < xs[mid]) hi = mid;
      else lo = mid;
    }
    return ys[lo] + (ys[hi] - ys[lo]) / (xs[hi] - xs[lo]) * (x - xs[lo]);
  };
}

function repeat(n, value) {
  return Array.apply(null, Array(n)).map(function () { return value })
}

function linspace(startValue, stopValue, cardinality) {
  var arr = [];
  var currValue = startValue;
  var step = (stopValue - startValue) / (cardinality - 1);
  for (var i = 0; i < cardinality; i++) {
    arr.push(currValue + (step * i));
  }
  return arr;
}

import pattern from 'patternomaly';

function chart_data_builder(x, y, m_y, G_x, G_y, xlabel){
  // special for the tax (tax and marginal)
  var n = x.length
  var data = []
  for (var i = 0; i < n; i++) {
    data.push({x:x[i], y:y[i]})
  }
  var m_data = []
  for (var i = 0; i < n; i++) {
    m_data.push({x:x[i], y:m_y[i]})
  }
  var G_data = []
  for (var i = 0; i < n; i++) {
    G_data.push({x:G_x[i], y:G_y[i]})
  }
  return {
  type: 'line',
  data: {
    labels: x,
    // labels: linspace(0, 300000, 6),
    datasets: [
      { 
        label: 'current year pension room',
        yAxisID: 'left',
        type: 'line',
        lineTension: 0,
        data: G_data,
        backgroundColor: pattern.draw('diagonal', 'rgba(255,255,0,0.25)'),
        borderColor: '#333333', // repeat(n, '#36495d'),
        borderWidth: 2
      },
      { 
        steppedLine: 'before', // before/after it is the oposite of what you think
        label: 'effective maginal rate',
        yAxisID: 'right',
        type: 'line',
        lineTension: 0,
        data: m_data,
        backgroundColor: pattern.draw('line-vertical', 'rgba(0,0,255,0.25)'),
        borderColor: '#333333', // repeat(n, '#36495d'),
        borderWidth: 2
      },
      { 
        label: 'gross tax',
        yAxisID: 'left',
        type: 'line',
        lineTension: 0,
        data: data,
        backgroundColor: pattern.draw('circle', 'rgba(100,0,0,0.25)'),
        borderColor: '#333333', // repeat(n, '#36495d'),
        borderWidth: 2
      }
      ]
    },
    options: {
      responsive: true,
      scales: {
        xAxes: [{
          type: 'linear',
          scaleLabel: {
              display: true,
              labelString: xlabel
          },
          ticks: {
            beginAtZero: true,
            suggestedMax: 20 // x.reduce((a, b) => Math.max(a, b))
            // padding: 25,
          },
        }],
        yAxes: [
          {
            id: 'left',
            scaleLabel: {
              display: true,
              labelString: 'gross tax'
            },
            ticks: {
              beginAtZero: true,
            }
          },
          {
            id: 'right',
            scaleLabel: {
              display: true,
              labelString: 'marginal rate'
            },
            position: 'right',
            ticks: {
              beginAtZero: true,
            }
          }
        ]
      }
    }
  }
}

